.hero-section {
    background: url('https://via.placeholder.com/1920x800') no-repeat center center/cover;
    height: 100vh;
    position: relative;
  }
  .hero-section::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6); /* Overlay effect */
    z-index: 1;
  }
  .hero-section .container {
    z-index: 2;
    position: relative;
  }
  